/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.seccion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 8rem 0;
  }
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.header__logo {
  width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__contenido {
  padding: 10rem 0;
}
@media (min-width: 1024px) {
  .header__contenido {
    padding-top: 15rem;
    padding-bottom: 15rem;
  }
}
.header__contenido h1 {
  color: #FFFFFF;
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .header__contenido h1 {
    font-size: 6rem;
    /* Para que el h1 tome un espacio especifico */
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .header__contenido h1 {
    max-width: 40%;
  }
}

.primario__btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.primario__btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .primario__btn {
    width: auto;
    display: inline-block;
  }
}

.precios__btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 2px solid #FFFFFF;
  margin-top: 2rem;
  display: block; /* En el ixing de botones tenemos display: inline-block */
}
.precios__btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .precios__btn {
    width: auto;
    display: inline-block;
  }
}

@media (min-width: 768px) {
  .distribucion__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.distribucion__imagen {
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .distribucion__imagen {
    width: 100%;
  }
}
.distribucion__contenido {
  padding-right: 8rem;
}
.distribucion__contenido h2 {
  font-size: 3.6rem;
  display: inline-block; /* Para que la linea tome solo el espacio del texto */
  color: #313c4e;
  text-align: center;
  margin: 0;
  padding-bottom: 2rem;
}
.distribucion__contenido h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: 103%; /* Para que la linea sobrepase un poco el espacio hacia la derecha */
  display: block;
  margin-top: -1.4rem;
}
@media (min-width: 768px) {
  .distribucion__contenido h2 {
    text-align: start;
    padding-bottom: 0;
  }
}
.distribucion__contenido p {
  line-height: 1.5;
  font-size: 2rem;
}

.alcance {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .alcance__grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
    gap: 5rem;
  }
}
.alcance__imagen {
  transition-property: transform;
  transition-duration: 300ms;
}
.alcance__imagen:hover {
  transform: rotate(8deg) scale(1.1);
}
.alcance__contenido h2 {
  font-size: 3.6rem;
  display: inline-block; /* Para que la linea tome solo el espacio del texto */
  color: #313c4e;
  text-align: center;
  margin: 0;
  padding-bottom: 2rem;
}
.alcance__contenido h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: 103%; /* Para que la linea sobrepase un poco el espacio hacia la derecha */
  display: block;
  margin-top: -1.4rem;
}
@media (min-width: 768px) {
  .alcance__contenido h2 {
    padding-bottom: 0;
  }
}
.alcance__contenido p {
  line-height: 1.5;
  font-size: 2rem;
  line-height: 1.5;
}

.estadisticas h2 {
  font-size: 3.6rem;
  display: inline-block; /* Para que la linea tome solo el espacio del texto */
  color: #313c4e;
  text-align: center;
  margin: 0;
}
.estadisticas h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: 103%; /* Para que la linea sobrepase un poco el espacio hacia la derecha */
  display: block;
  margin-top: -1.4rem;
}

.planes {
  background-color: #313c4e;
  text-align: center;
}
.planes__titulo {
  font-size: 3.6rem;
  display: inline-block; /* Para que la linea tome solo el espacio del texto */
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}
.planes__titulo::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: 103%; /* Para que la linea sobrepase un poco el espacio hacia la derecha */
  display: block;
  margin-top: -1.4rem;
}
@media (min-width: 768px) {
  .planes {
    text-align: start;
  }
}
.planes__texto {
  color: #FFFFFF;
  line-height: 1.6;
  font-size: 2rem;
}

.precios {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .precios {
    flex-direction: row;
    justify-content: center;
    /* Para achicar el contenedor de los precios y centrarlo */
    max-width: 80rem;
    margin-inline: auto; /* centrar horizontalmente */
    margin-block-start: 10rem; /* Margin top */
  }
}
.precios__plan {
  flex-grow: 1; /* Para que crezca en la proporcion de 1 */
  background-color: #FFFFFF;
  padding: 16rem 4rem 4rem 4rem;
  border-bottom: 1px solid #e1e1e1;
  /* &. Se utiliza cuando la clase estan en el mismo nivel */
}
@media (min-width: 768px) {
  .precios__plan {
    border-bottom: none;
    transition-property: transform;
    transition-duration: 300ms;
  }
  .precios__plan:hover {
    transform: scale(1.1);
  }
}
.precios__plan.inicio {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-size: 10rem;
  /* 50% horizontal y 2rem arriba y abajo */
  background-position: 50% 2rem;
}
.precios__plan.negocio {
  background-color: #84d9bc;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-size: 10rem;
  /* 50% horizontal y 2rem arriba y abajo */
  background-position: 50% 2rem;
  order: -1; /* Para que en un celular se muestre 1° */
}
@media (min-width: 768px) {
  .precios__plan.negocio {
    order: unset; /* Vuelve al orden del html */
    margin: -5rem 0; /* Para se extienda hacia arriba */
    border-radius: 2rem;
    /* reescribimos parte del codigo para que quede centrado con los demas */
    background-position: 50% 7rem;
    padding-top: 21rem;
    z-index: 100; /* Le decimos que este elemento este adelante de los otros */
  }
}
.precios__plan.pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-size: 10rem;
  /* 50% horizontal y 2rem arriba y abajo */
  background-position: 50% 2rem;
}
.precios__texto {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.precios__listados li {
  text-align: center;
  margin-bottom: 2rem;
}
.precios__listados li:last-of-type {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 4rem;
    align-items: center;
  }
}
.testimoniales .testimonial {
  padding-top: 5rem;
  position: relative;
}
@media (min-width: 768px) {
  .testimoniales .testimonial {
    padding-top: 0;
    /* No agarra el nth-child con (1), no se sabe porque. Por eso empieza con el (2) */
  }
  .testimoniales .testimonial__parrafo {
    max-width: 70%;
  }
  .testimoniales .testimonial__parrafo:nth-child(2) {
    margin-left: 10%;
  }
  .testimoniales .testimonial__parrafo:nth-child(3) {
    margin-left: 20%;
  }
  .testimoniales .testimonial__parrafo:nth-child(4) {
    margin-left: 30%;
  }
}
.testimoniales .testimonial h2 {
  font-size: 3.6rem;
  display: inline-block; /* Para que la linea tome solo el espacio del texto */
  color: #313c4e;
}
.testimoniales .testimonial h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: 103%; /* Para que la linea sobrepase un poco el espacio hacia la derecha */
  display: block;
  margin-top: -1.4rem;
}
.testimoniales .testimonial__parrafo {
  background-color: #84d9bc;
  padding: 2rem;
  border: 0.5rem solid #313c4e;
  margin-bottom: 4rem;
  padding: 2rem;
  border-radius: 1rem;
}
.testimoniales .testimonial__parrafo::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(/build/img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
}
.testimoniales .testimonial__parrafo:last-of-type {
  margin-bottom: 0;
}
.testimoniales .testimonial__parrafo p {
  margin: 0;
  padding-left: 6rem;
  font-size: 1.6rem;
}

.footer {
  background-color: #313c4e;
}
.footer__grid {
  border-bottom: 1px solid #84d9bc;
  padding: 5rem 0 2rem 0;
}
.footer__imagen {
  max-width: 20rem;
  margin-bottom: 5rem;
  align-items: center;
}
.footer__contenido h3 {
  color: #84d9bc;
  text-align: left;
  padding: 1rem 0;
}
.footer__contenido h3:last-child {
  padding-bottom: 3rem;
}
.footer__contenido a {
  display: block;
  text-align: left;
  color: #fff;
  margin-bottom: 5px;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    border-bottom: 1px solid #84d9bc;
  }
  .footer__grid__contenido {
    margin-bottom: 0;
  }
  .footer__grid h3 {
    color: #84d9bc;
    text-align: left;
    padding: 0;
  }
  .footer nav a {
    display: block;
    text-align: left;
    color: #fff;
    margin-bottom: 5px;
  }
}
.footer .copyright p {
  margin: 0;
  padding: 1rem;
  font-size: 1.6rem;
  text-align: center;
  color: #fff;
}